'use client';

import { notFound, useSearchParams } from 'next/navigation.js';
import { ReactElement } from 'react';

import { WithAuth } from '@/app/guards/index.js';
import { PrimaryPage } from '@/app/navigation/index.js';

import { SlidesPage } from '@/pages/slides/index.js';

import { SlidesConsumer } from '@/entities/slides/index.js';

const Slides = (): ReactElement => {
    const searchParams = useSearchParams();

    const artistId = searchParams.get('artistId');

    if (!artistId) {
        notFound();
    }

    return (
        <PrimaryPage>
            <WithAuth>
                <SlidesPage slidesConsumer={SlidesConsumer.ARTIST} artistId={artistId} />
            </WithAuth>
        </PrimaryPage>
    );
};

// eslint-disable-next-line import/no-default-export
export default Slides;
